* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

*:before,
*:after {
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}

p {
  font-size: 2rem;
}

a {
  text-decoration: none;
  color: inherit;
}

h1, h2, h3 {
  margin-bottom: 4rem;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

video,
img {
  display: block;
  max-width: 100%;
}

img {
  width: 100%;
  height: auto;
}

ul,
ol {
  list-style: none;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin-inline: auto;
}

@media (min-width: 768px) {
  .sticky {
    position: sticky;
    top: 0;
    z-index: 1;
  }
}
.bg-primario {
  background-color: #4cb8b3;
}
.bg-primario li:nth-child(odd) {
  background-color: #3b9691;
}

.bg-secundario {
  background-color: #f53756;
}
.bg-secundario li:nth-child(odd) {
  background-color: #ed0c31;
}

.bg-resaltado {
  background-color: #7072ec;
}
.bg-resaltado li:nth-child(odd) {
  background-color: #4346e6;
}

.header {
  color: #fff;
  background-color: #4cb8b3;
}

.contenido-header {
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 2rem;
}
@media (max-width: 768px) {
  .contenido-header {
    flex-direction: column;
  }
}
.contenido-header h2 {
  margin: 0;
}

.navegacion-principal {
  display: flex;
  gap: 1.6rem;
}
@media (max-width: 768px) {
  .navegacion-principal {
    flex-direction: column;
    margin-top: 3rem;
  }
}
.navegacion-principal a {
  font-size: 2.4rem;
}
.navegacion-principal a:hover {
  color: #7072ec;
}

.video {
  position: relative;
  max-height: 70rem;
}
.video .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  background: rgb(221, 0, 110);
  background: linear-gradient(90deg, rgba(221, 0, 110, 0.3) 0%, rgba(155, 3, 255, 0.5) 100%);
}
.video .video-contenido {
  color: #fff;
}
@media (max-width: 768px) {
  .video .video-contenido {
    text-align: center;
  }
}
.video .video-contenido h1 {
  margin: 0;
  font-size: 7rem;
}
@media (max-width: 768px) {
  .video .video-contenido h1 {
    font-size: 5rem;
  }
}
.video .video-contenido p {
  display: inline-block;
  padding: 1rem 1.5rem;
  font-weight: bold;
  background-color: #f53756;
}
.video video {
  width: 100%;
  min-height: 40rem;
  max-height: 70rem;
  object-fit: cover;
  object-position: bottom;
}

.sobre-festival {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 4rem;
  padding-block: 5rem;
}
@media (max-width: 768px) {
  .sobre-festival {
    grid-template-columns: 1fr;
  }
}

.contenido-festival {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.contenido-festival .fecha {
  color: #f53756;
  font-weight: bold;
}
.contenido-festival p {
  margin-bottom: 2rem;
}

.lineup {
  padding-block: 4rem;
  margin-bottom: 2rem;
  background-color: #f53756;
}
.lineup h3,
.lineup .dia {
  color: #fff;
  text-align: center;
}
.lineup .dia {
  text-transform: uppercase;
  margin-bottom: 4rem;
}
.lineup p {
  font-weight: 300;
  text-align: center;
}

.escenarios-contenedor {
  margin-bottom: 2rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 5rem;
}
@media (max-width: 768px) {
  .escenarios-contenedor {
    grid-template-columns: 1fr;
  }
}
.escenarios-contenedor .rock p::after {
  background-image: url("../../build/img/icono_rock.webp");
}
.escenarios-contenedor .EDM p::after {
  background-image: url("../../build/img/icono_edm.webp");
}

.escenario {
  padding-top: 4rem;
}
.escenario p::after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  margin: 1rem auto;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}
.escenario .calendario {
  font-weight: bold;
}
.escenario .calendario li {
  padding: 2.5rem;
}
.escenario .calendario span {
  font-weight: normal;
}

.galeria {
  text-align: center;
}
.galeria .galeria-imagenes {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(17rem, 1fr));
  gap: 1rem;
  margin-block: 2rem;
}

.overlay-imagen {
  position: fixed;
  top: 0;
  display: grid;
  place-items: center;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
}
.overlay-imagen picture {
  max-width: min(80rem, 95%);
}
.overlay-imagen .boton-modal {
  padding: 1rem 1.7rem;
  font-size: 3.5rem;
  font-weight: 900;
  border-radius: 50%;
  color: #000;
  background-color: #fff;
  cursor: pointer;
  transition: all 0.4s ease;
}
.overlay-imagen .boton-modal:hover {
  background-color: #b3b3b3;
  transform: rotate(180deg);
}

.boletos {
  padding-block: 5rem;
  background-color: #4cb8b3;
}
.boletos h3 {
  text-align: center;
  font-size: 2rem;
  color: #fff;
}
.boletos p {
  text-align: center;
}

.contenedor-boletos {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(30rem, 1fr));
  gap: 2rem;
  color: #fff;
}
.contenedor-boletos .pase {
  margin-top: 1rem;
}
.contenedor-boletos .pase .pase-header {
  padding: 1rem;
  text-transform: uppercase;
  font-weight: lighter;
  color: #fff;
  background-color: #f53756;
}
.contenedor-boletos .pase .pase-body {
  color: #000;
  background-color: #fff;
}
.contenedor-boletos .pase .pase-body li {
  padding: 1rem;
  border-bottom: 1px solid #e4e4e4;
}
.contenedor-boletos .pase .pase-footer {
  padding: 1rem;
  font-size: 2.4rem;
  background-color: #752f97;
}

.footer {
  padding: 1rem;
  text-align: center;
  color: #fff;
  background-color: #f53756;
}
.footer p {
  font-size: 1.6rem;
}